import { useMedia } from 'the-platform/useMedia';

import { BREAKPOINTS } from './breakpoints';

export const useXlg = () => useMedia(BREAKPOINTS.xlg, false);
export const useLg = () => useMedia(BREAKPOINTS.lg, false);
export const useMd = () => useMedia(BREAKPOINTS.md, false);
export const useSm = () => useMedia(BREAKPOINTS.sm, false);

export const useBreakpoint = () => {
  const breakpoints = [
    { name: 'sm', value: useSm() },
    { name: 'md', value: useMd() },
    { name: 'lg', value: useLg() },
    { name: 'xlg', value: useXlg() }
  ];

  return (defaultValue, breakpointValues = {}) => {
    for (const i in breakpoints) {
      if (!breakpoints.hasOwnProperty(i)) {
        continue;
      }
      const bp = breakpoints[i];

      if (bp.value && bp.name in breakpointValues) {
        return breakpointValues[bp.name];
      }
    }

    return defaultValue;
  };
};
